<template>
  <view>
    <div class="searchForm">
      <div class="searchInput" @click="openStatusPicker">
        {{ statusText || '全部状态' }}
        <uv-icon name="arrow-down-fill" size="6" height="4" color="#303133"></uv-icon>
      </div>
      <div class="searchInput" @click="openTypePicker">
        {{ typeText || '全部类型' }}
        <uv-icon name="arrow-down-fill" size="6" height="4" color="#303133"></uv-icon>
      </div>
      <!-- <image class="searchIcon" src="https://cdn-static.papa.com.cn/jingAn/icon/black-search.svg" mode="" /> -->
    </div>
    <!-- 状态弹窗 -->
    <uv-action-sheet
      round
      ref="statusSheet"
      :actions="statusList"
      name="label"
      title="请选择状态"
      @select="
        (e) => {
          search(e, 'status')
        }
      "
    ></uv-action-sheet>
    <!-- 类型弹窗 -->
    <!--  @close="close" -->
    <uv-action-sheet
      round
      ref="typeSheet"
      :actions="typeList"
      title="请选择类型"
      @select="
        (e) => {
          search(e, 'type')
        }
      "
    ></uv-action-sheet>
  </view>
</template>

<script>
export default {
  props: {
    statusList: {
      type: Array,
      default: () => [],
    },
    typeList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      statusText: '全部状态',
      typeText: '全部类型',
    }
  },
  methods: {
    openStatusPicker() {
      this.$refs.statusSheet.open()
    },
    openTypePicker() {
      this.$refs.typeSheet.open()
    },
    search(e, type) {
      console.log({ e, type })
      // dictId serveWorkDevStatus:platform_wait
      if (type == 'status') {
        this.statusText = e.name
        this.$emit('search', { serveWorkDevStatus: e.value })
      } else if (type == 'type') {
        this.typeText = e.name
        this.$emit('search', { dictId: e.dictId })
      }
    },
  },
}
</script>

